
#app {
  margin-top: 1rem;
}
.wcounter {
    margin-top: 0.5rem;
}
.button-block {
    margin-top: 0.5rem;
}
.btn-history {
    margin-top: 0.5rem;
}
.button-block .btn-chuv {
    margin-right: 0.2rem;
}
.button-block .btn-chuv:last-child {
    margin-right: 0;
}
@media (max-width: 400px) {
.navbar-header .third {
        display:none;
}
}
@media (min-width: 400px) and (max-width: 767px) {
.navbar-header .first {
        display:none;
}
}
@media (max-width: 767px) {
.navbar-header .second {
        display:none;
}
}
@media (max-width: 767px) {
.third-part {
        display:none;
}
.button-block {
        text-align: center;
}
}



